body {
  
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
}
.Header {
  padding: 15px 10px;
  background: #333;
  
}
.Header a {
  color: #FFF;
  text-decoration: none;
}
.Header a:hover {
  text-decoration: underline;
}
.Demo {
  background: #FC466B;
  background: linear-gradient(to top, #3F5EFB, #FC466B);
  padding: 20px 0px 80px 0;
}
.Card {
  background: #FFF;
  margin: 0 auto;
  max-width: 400px;
  min-width: 200px;
  width: 100%;  
  padding: 20px;
  border-radius: 4px;
  box-shadow: 2px 10px 74px -9px rgba(0,0,0,0.75);
}

.Actions {
  color: #FFF;
  background: #333;
  margin: 0 auto;
  max-width: 400px;
  min-width: 200px;
  width: 100%;  
  padding: 20px;
}

.Actions img{ margin: 20px auto; display: block; border: 30px solid #FFF;}

.App ul, .App li {
  padding: 0;
  margin: 0;
  list-style: none;
}

small {
  color: #666;
  font-size: 10px;
}

.container {
  max-width: 800px;
  margin: 50px auto;
}

.Button {
  box-shadow:inset 0px 1px 0px 0px #9acc85;
  background:linear-gradient(to bottom, #74ad5a 5%, #68a54b 100%);
  filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#74ad5a', endColorstr='#68a54b',GradientType=0);
  background-color:#74ad5a;
  border:1px solid #3b6e22;
  display:inline-block;
  cursor:pointer;
  color:#ffffff;
  font-family:Arial;
  font-size:19px;
  padding:12px 18px;
  text-decoration:none;
  margin: 15px 0;
}
.Button:hover {
  background:linear-gradient(to bottom, #68a54b 5%, #74ad5a 100%);
  filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#68a54b', endColorstr='#74ad5a',GradientType=0);
  background-color:#68a54b;
}
.Button:active {
  position:relative;
  top:1px;
}

footer {
  padding: 30px 50px;
  color: #FFF;
  background: #373F51;
}
footer a {
  color: #FFF;
}

.Page-Header {
  padding: 40px;
  text-align: center;
}
.Page-Title {
  font-family: 'Shrikhand', cursive;
  font-weight: normal;
  color: #FFF;
  font-size: 72px;
  text-shadow: -2px 2px 0px rgba(0,0,0, .3);
  margin-bottom: 25px;
}
.Page-Description {
  margin-top: 0;
  color: #FFF;
  font-family: 'Poiret One', cursive;
  font-size: 32px;
}

.Page-Description a {
  color: #FFF;
}

.Page-Description span {
  font-size: 18px;
}

.container h1 {
  margin: 30px 0;
}

.highlight {
  border-radius: 4px;
  color: #FFF;
  display: inline-block;
  padding: 2px 10px;
  background: #2196f3;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #f44336, #2196f3); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  
}
